import { Component, OnInit, Input, OnDestroy, Renderer2 } from '@angular/core';
import { NavigationService } from "../../../shared/services/navigation.service";
import { Subscription } from 'rxjs';
import { ThemeService } from '../../../shared/services/theme.service';
import { TranslateService } from '@ngx-translate/core';
import { LayoutService } from '../../services/layout.service';
import { JwtAuthService } from 'app/shared/services/auth/jwt-auth.service';
//import { User } from 'app/shared/models/user.model';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { MatDialog } from '@angular/material/dialog';
import { SearchComponent } from 'app/views/others/search/search.component';
import { DialogService } from 'app/views/others/Dialogs/dialog.service';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';

@Component({
  selector: 'app-header-top',
  templateUrl: './header-top.component.html',
  styleUrls: ['./header-top.component.scss']
})
export class HeaderTopComponent implements OnInit, OnDestroy {

  layoutConf: any;
  menuItems: any;
  user: any;
  menuItemSub: Subscription;
  rumangoThemes: any[] = [];
  currentLang = 'en';
  availableLangs = [{
    name: 'English',
    code: 'en',
  }, {
    name: 'Spanish',
    code: 'es',
  }]
  @Input() notificPanel;
  @Input() profilePanel;
  flag: any;
  flag1: any;
  currentUser: any;
  constructor(
    private layout: LayoutService,
    private router: Router,
    private navService: NavigationService,
    public themeService: ThemeService,
    public translate: TranslateService,
    private renderer: Renderer2,
    public jwtAuth: JwtAuthService,
    private ls: LocalStoreService,
    private dialog: MatDialog,
    private apiService: ApiService
  ) { }

  ngOnInit() {
    this.currentUser=this.ls.getItem("ICUST_USER");
    this.flag=this.ls.getItem("notification");
    this.flag1=this.ls.getItem("profile");
    this.layoutConf = this.layout.layoutConf;
    this.rumangoThemes = this.themeService.rumangoThemes;
    this.menuItemSub = this.navService.menuItems$
      .subscribe(res => {
        res = res.filter(item => item.type !== 'icon' && item.type !== 'separator');
        let limit = 4
        let mainItems: any[] = res.slice(0, limit)
        if (res.length <= limit) {
          return this.menuItems = mainItems
        }
        let subItems: any[] = res.slice(limit, res.length - 1)
        mainItems.push({
          name: 'More',
          type: 'dropDown',
          tooltip: 'More',
          icon: 'more_horiz',
          sub: subItems
        })
        this.menuItems = mainItems
      })
    this.user = this.ls.getItem('ICUST_USER');
  }
  ngOnDestroy() {
    this.menuItemSub.unsubscribe()
  }
  setLang() {
    this.translate.use(this.currentLang)
  }
  changeTheme(theme) {
    //this.layout.publishLayoutChange({ matTheme: theme.name }) 
      this.layout.applyMatTheme(theme);
  }
   onChange(t): void {
    console.log("t", t);
     if (t.checked) {
      this.themeService.changeTheme('egret-navy','egret-dark-purple');
    } else {
        this.themeService.changeTheme('egret-dark-purple','egret-navy');
      }
    }

  toggleNotific() {

    this.ls.setItem("notification", true);
    this.flag = this.ls.getItem('notification');
    this.notificPanel.toggle();
  }
  toggleProfile() {
    console.log("call profile");
    this.ls.setItem("profile", true);
    this.flag1 = this.ls.getItem('profile');
    this.profilePanel.toggle();
    console.log("next one");
  }
  toggleSidenav() {
    if (this.layoutConf.sidebarStyle === 'closed') {
      return this.layout.publishLayoutChange({
        sidebarStyle: 'full'
      })
    }
    this.layout.publishLayoutChange({
      sidebarStyle: 'closed'
    })
  }
    //search implementation
    onSearch(){
      const dialogRef = this.dialog.open(SearchComponent,{
        width: '50%',
        disableClose: true,
        panelClass:"custom",
      });
  
      dialogRef.afterClosed().subscribe(result => {
        console.log(`Dialog result: ${result}`);
      });
    }
    goToSettings(){
     console.log("enter inside settings");
     this.router.navigate(["/settings/profile"]);
    }
    goToPassword(){
      this.router.navigate(['/settings/change-password']);
    }
    goToMaintenance() {
      this.router.navigate(['/maintenance']);
    }
    gotoEmailchange(){
      this.router.navigate(['/settings/change-email']);
    }
    goToThemeChange(){
      this.router.navigate(['/settings/theme-change']);
    }

    onSignout(username) {
      console.log("Inside signout method");
      this.apiService.appLogout(username).subscribe((resp) => {
        console.log("resp ", resp);
        if(resp != null) {
          this.jwtAuth.signout();
        }
      });
    }
  
}
