import { Injectable } from "@angular/core";
import { LocalStoreService } from "../local-store.service";
import { HttpClient } from "@angular/common/http";
import { Router, ActivatedRoute } from "@angular/router";
import { of, BehaviorSubject, throwError } from "rxjs";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class JwtAuthService {
  token;
  isAuthenticated: boolean;
  user: any = {};
  user$ = (new BehaviorSubject<any>(this.user));
  signingIn: boolean;
  return: string;
  APP_USER = "ICUST_USER";
  private baseUrl = environment.DESKTOP_URL;
  constructor(
    private ls: LocalStoreService,
    private http: HttpClient,
    private router: Router,
    private route: ActivatedRoute
  ) {
    this.route.queryParams
      .subscribe(params => this.return = params['return'] || '/');
  }

  public signout() {
    this.setUserAndToken(null, false);
    this.ls.setItem("notification", false);
    this.ls.setItem("browserId", null);
    this.ls.setItem("denomRequired",null)
    this.router.navigateByUrl("sessions/signin");
  }

  isLoggedIn(): boolean {
    return !!this.getAuthStatus();
  }

  getAuthStatus() {
    return this.ls.getItem('isAuthenticated');
  }

  getUser() {
    return this.ls.getItem(this.APP_USER);
  }

  encodeData(data: any) {
    return btoa(data);
  }

  decodeData(data: any) {
    return atob(data)
  }

  getEncryptedToken() {
    let user: any = this.getUser();
    let encodedUser = this.encodeData(JSON.stringify(user));
    return encodedUser;
  }

  getAccessToken(): string {
    let user: any = this.getUser();
    let accessToken = user['jwtResp']?.accessToken;
    if (accessToken != null) {
      return accessToken || null;
    }
  }

  setUserAndToken(user?: any, isAuthenticated?: boolean) {
    this.isAuthenticated = isAuthenticated;
    this.ls.setItem("isAuthenticated", isAuthenticated);
    this.ls.setItem(this.APP_USER, user);
    this.user$.next(user);
  }

  loginIcut(login) {
    return this.http.post<any>(`${this.baseUrl}/loginApi/credentials`, login);
  }
}
