import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Observable, Subject } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class DataService {

  private auditLogData: any = {};

  private paramSource = new BehaviorSubject({});
  getNavParam = this.paramSource.asObservable();

  private searchItem = new BehaviorSubject({});
  getSearchItem = this.searchItem.asObservable();

  private sendCustomerId = new BehaviorSubject("");
  getCustomerId = this.sendCustomerId.asObservable();

  private item = new BehaviorSubject({});
  getBankItem = this.item.asObservable();

  private navSource = new BehaviorSubject<any>({});
  currentNav = this.navSource.asObservable();

  private items = new BehaviorSubject({});
  getTillVaultMap = this.items.asObservable();

  private vaultStatus = new BehaviorSubject({});
  getTillVaultStatus = this.vaultStatus.asObservable();

  private TrxnCashStatus = new BehaviorSubject({});
  getTrxnCashMaster = this.TrxnCashStatus.asObservable();

  private denomMasterStatus = new BehaviorSubject({});
  getDenomMasterStatus = this.denomMasterStatus.asObservable();

  private CcyBalStatus = new BehaviorSubject({});
  getCcyBal = this.CcyBalStatus.asObservable();

  private accTypeStatus = new BehaviorSubject({});
  getAccType = this.accTypeStatus.asObservable();

  private baseDenomStatus = new BehaviorSubject({});
  getBaseDenom = this.baseDenomStatus.asObservable();

  private vaultConfig = new BehaviorSubject({});
  getTillVaultConfig = this.vaultConfig.asObservable();

  private menuSource = new BehaviorSubject("");
  getMenus = this.menuSource.asObservable();

  private childMenuData = new BehaviorSubject<any>([]);
  getchildMenuDetails() {
    return this.childMenuData.asObservable();
  }
  setchildMenuDetails(childMenuData) {
    this.childMenuData.next(childMenuData);
  }

  constructor() { }



  changeNav(message: string) {
    this.navSource.next(message)
  }

  sendNavParam(params: object) {
    this.paramSource.next(params)
  }

  shareSearchItem(params: object) {
    this.searchItem.next(params)
  }

  shareCustomerId(customerId) {
    this.sendCustomerId.next(customerId);
  }

  shareBankItem(item: any) {
    this.item.next(item);
  }

  setAuditLogData(data) {
    console.log(data);
    this.auditLogData = data;
  }

  getAuditLogData() {
    return this.auditLogData;
  }

  private subject = new Subject<any>();

  sendClickEvent() {
    this.subject.next();
  }

  getClickEvent(): Observable<any> {
    return this.subject.asObservable();
  }

  shareAccTypeItem(item: any) {
    this.item.next(item);
  }

  shareTillVaultItem(item: any) {
    this.item.next(item);
  }


  shareTillVaultStatus(item: any) {
    this.item.next(item);
  }

  private subjectOfIcustUser = new Subject<any>();

  onChangeOfIcustUser(value) {
    this.subjectOfIcustUser.next(value);
  }

  getIcustUser(): Observable<any> {
    return this.subjectOfIcustUser.asObservable();
  }
}