  letterOnly(event) {
    var charCode = event.keyCode;
    if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
      return true;
    else
      return false;
  }
  onSearch(event) {
    let value = event.target.value
    value = value.toLowerCase();
    console.log(value);
    this.flag = false;
    this.menuItems = this.setMenuItem();
    if (value.length > 0) {
      this.menuItems = this.menuItems.filter(item => item.name.slice(0, value.length).toLowerCase() == value || item.type == 'dropDown')
      this.menuItems.map(item => {
        if (item.type == 'dropDown') {
          item.sub = item.sub.filter(sub => sub.name.slice(0, value.length).toLowerCase() == value || sub.type == 'dropDown')
          item.sub.map(sub => {
            if (sub.type == 'dropDown') {
              sub.sub = sub.sub.filter(underSub => underSub.name.slice(0, value.length).toLowerCase() == value)
              if (sub.name.slice(0, value.length).toLowerCase() == value && sub?.sub.length == 0) {
                let subMenu = this.setMenuItem();
                if (sub.name == 'Teller') {
                  sub.sub = subMenu[1].sub[29].sub
                }
                else if (sub.name == 'Holiday Maintenance') {
                  sub.sub = subMenu[1].sub[11].sub
                }
              }
              item.sub = item.sub.filter(i => !i.sub || i?.sub.length != 0 || i.name.slice(0, value.length).toLowerCase() == value)
            }
          })
          if (item.name.slice(0, value.length).toLowerCase() == value && item?.sub.length == 0) {
            let maintenance = this.setMenuItem()
            item.sub = maintenance[1].sub
          }
          this.menuItems = this.menuItems.filter(item => !item.sub || item?.sub.length != 0 || item.name.slice(0, value.length).toLowerCase() == value)

        }
      })
      if (this.menuItems.length == 0) {
        this.flag = true;
      }
      else {
        this.flag = false;
      }
    }
    else {
      this.flag = false;
    }

  }
